<template>
  <div>
    <el-menu
        :default-openeds="['1', '3']"
        style="height: 100%; overflow-x: hidden"
        background-color="rgb(48,65,86)"
        text-color="#fff"
        active-text-color="#ffd04b"
        :collapse-transition="false"
        :collapse="isCollapse"
        :router="true"
    >
      <div style="height: 60px; line-height: 60px; text-align: center">
        <img
            src="../assets/logo.png"
            style="
                width: 20px;
                position: relative;
                top: 3px;
                margin-right: 3px;
              "
        />
        <b style="color: white" v-show="logTextShow">后台管理系统</b>
      </div>


      <div v-for="item in menus" :key="item.id">
        <div v-if="item.path">
          <el-menu-item :index="item.path">
              <i :class="item.icon"></i>
              <span slot="title">{{ item.name }}</span>
          </el-menu-item>
        </div>

        <div v-else>
          <el-submenu :index="item.id+''">
            <template slot="title">
            <i :class="item.icon"></i>
            <span>{{ item.name }}</span>
            </template>
            <div v-for="subItem in item.children" :key="subItem.id">
              <el-menu-item :index="subItem.path">
                <template slot="title">
                <i :class="subItem.icon"></i>
                <span>{{ subItem.name }}</span>
                </template>
              </el-menu-item>
            </div>
          </el-submenu>
        </div>
      </div>



    </el-menu>
  </div>
</template>

<script>
export default {
  name: "Aside",
  data() {
    return {
      menus: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")).menus : [],
    };
  },
  props:{
    isCollapse:Boolean,
    logTextShow:Boolean
  },
  methods:{
    }
}
</script>

