<template>
  <div class="home" style="height: 100%">
    <el-container style="height: 100%">
      <el-aside
          :width="sideWidth + 'px'"
          style="
          background-color: rgb(238, 241, 246);
          box-shadow: 2px 0 6px rgb(0 21 41/35%);
        "
      >
        <Aside :isCollapse="isCollapse" :logTextShow="logTextShow"/>
      </el-aside>
      <el-container>

        <el-header style="border-bottom: 1px solid #ccc" >
          <Header :collapse="collapse" :collapseBtnClass="collapseBtnClass" />
        </el-header>


        <el-main>
<!--          表示当前页面的子路由会在<router-view/>这个位置显示-->
          <router-view/>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";
import Aside from  "@/components/Aside";
import Header from "@/components/Header";
// import { TimeSelect } from 'element-ui'

export default {
  name: "HomeView",
  components: {
    Aside,
    HelloWorld,
    Header,
  },
  data() {
    return {
      collapseBtnClass: "el-icon-s-unfold",
      isCollapse: false,
      sideWidth: 200,
      logTextShow: true,
    };
  },
  created() {
  },
  methods: {
    collapse() {
      //  点击收缩按钮触发
      this.isCollapse = !this.isCollapse;
      if (this.isCollapse) {
        this.sideWidth = 65;
        this.collapseBtnClass = "el-icon-s-fold";
        this.logTextShow = false;
      } else {
        this.sideWidth = 200;
        this.collapseBtnClass = "el-icon-s-unfold";
        this.logTextShow = true;
      }
    }
  },
};
</script>

<style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
</style>
