<template>
  <div STYLE="font-size: 12px; display: flex">
    <div style="flex: 1; ">
            <span
                :class="collapseBtnClass"
                style="cursor: pointer; font-size: 18px"
                @click="collapse"

            ></span>
      <el-breadcrumb separator="&" style="display: inline-block;margin-left: 10px">
        <el-breadcrumb-item :to="'/home'">首页</el-breadcrumb-item>
        <el-breadcrumb-item>{{ currentPathName }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-dropdown style="width: auto;height: 60px">
      <div>
              <span class="el-dropdown-link">
              <img
                  :src="user.avatar"
                  alt=""
                  style="width: 50px; height: 50px; border-radius: 50%;margin: 5px 10px"
              /></span>
        <span style="float: right">{{ user.nickname }}
              <i
                  class="el-icon-arrow-down"
                  style="margin-left: 5px; cursor: pointer"
              ></i>
            </span>
      </div>
      <el-dropdown-menu slot="dropdown" style="width: 150px;  text-align: center ">
        <el-dropdown-item>
          <router-link to="person" style="text-decoration: none">个人信息</router-link>
        </el-dropdown-item>
        <el-dropdown-item>
          <span style="text-decoration: none" @click="loginOut"> 退出</span>
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>

  </div>
</template>

<script>
import store from "@/store";

export default {
  name: "Header",
  props: {
    collapseBtnClass: String,
    collapse: Function,
  },
  methods:{
    loginOut(){
      store.commit("loginOut")
    }
  },
  data() {
    return {
      // 从localStorage中获取用户信息,?表示如果没有,则返回空对象,否则返回JSON对象
      user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
    }
  },
  computed: {
    currentPathName() {
      return store.state.currentPathName
    }
  },
  watch: {
    currentPathName(newVal, oldVal) {
      console.log(newVal)
    }
  },
}
</script>

<style scoped>

</style>